// src/app/(dashboard)/tools/page.tsx
'use client';

import React, { useState, useEffect } from 'react';
import { useQuery } from '@tanstack/react-query';
import axios from 'axios';
import ToolCard from '@/components/tools/ToolCard';
import ToolComparison from '@/components/tools/ToolComparison';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Search, Filter, X } from 'lucide-react';
import { ITool } from '@/models/Tool';

// Tool categories for filtering
const CATEGORIES = [
  { value: 'all', label: 'All Categories' },
  { value: 'STUDY_PLANNING', label: 'Study Planning' },
  { value: 'NOTE_TAKING', label: 'Note Taking' },
  { value: 'PRACTICE_TESTS', label: 'Practice Tests' },
  { value: 'VIDEO_LEARNING', label: 'Video Learning' },
  { value: 'PROBLEM_SOLVING', label: 'Problem Solving' },
  { value: 'FORMULA_SHEETS', label: 'Formula Sheets' },
  { value: 'CALCULATORS', label: 'Calculators' },
  { value: 'COLLABORATION', label: 'Collaboration' },
  { value: 'FOCUS_PRODUCTIVITY', label: 'Focus & Productivity' },
  { value: 'EXAM_ANALYSIS', label: 'Exam Analysis' },
];

const SORT_OPTIONS = [
  { value: 'featured', label: 'Featured' },
  { value: 'popular', label: 'Most Popular' },
  { value: 'newest', label: 'Newest' },
  { value: 'name', label: 'Name (A-Z)' },
];

export default function ToolsPage() {
  const [searchQuery, setSearchQuery] = useState('');
  const [category, setCategory] = useState('all');
  const [sortBy, setSortBy] = useState('featured');
  const [compareList, setCompareList] = useState<string[]>([]);
  const [showComparison, setShowComparison] = useState(false);
  
  // Fetch tools
  const { data, isLoading, error } = useQuery({
    queryKey: ['tools', searchQuery, category, sortBy],
    queryFn: async () => {
      const params = new URLSearchParams();
      if (searchQuery) params.append('search', searchQuery);
      if (category !== 'all') params.append('category', category);
      params.append('sort', sortBy);
      
      const response = await axios.get(`/api/tools?${params}`);
      return response.data;
    },
  });
  
  // Fetch comparison data
  const { data: comparisonData } = useQuery({
    queryKey: ['comparison', compareList],
    queryFn: async () => {
      if (compareList.length < 2) return null;
      const response = await axios.post('/api/tools/compare', {
        toolIds: compareList
      });
      return response.data;
    },
    enabled: compareList.length >= 2,
  });
  
  // Handle adding/removing tools from comparison
  const handleCompare = (toolId: string) => {
    setCompareList(prev => {
      if (prev.includes(toolId)) {
        return prev.filter(id => id !== toolId);
      }
      if (prev.length >= 4) {
        alert('You can compare up to 4 tools at a time');
        return prev;
      }
      return [...prev, toolId];
    });
  };
  
  const removeFromComparison = (toolId: string) => {
    setCompareList(prev => prev.filter(id => id !== toolId));
    if (compareList.length <= 2) {
      setShowComparison(false);
    }
  };
  
  return (
    <div className="container mx-auto py-8 px-4">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold mb-2">Study Tools Directory</h1>
        <p className="text-muted-foreground">
          Discover the best tools to ace your JEE and NEET preparation
        </p>
      </div>
      
      {/* Filters */}
      <Card className="mb-8">
        <CardContent className="p-6">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            {/* Search */}
            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Search tools..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
            
            {/* Category Filter */}
            <Select value={category} onValueChange={setCategory}>
              <SelectTrigger>
                <SelectValue placeholder="Category" />
              </SelectTrigger>
              <SelectContent>
                {CATEGORIES.map(cat => (
                  <SelectItem key={cat.value} value={cat.value}>
                    {cat.label}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            
            {/* Sort */}
            <Select value={sortBy} onValueChange={setSortBy}>
            <SelectTrigger>
                <SelectValue placeholder="Sort by" />
              </SelectTrigger>
              <SelectContent>
                {SORT_OPTIONS.map(option => (
                  <SelectItem key={option.value} value={option.value}>
                    {option.label}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            
            {/* Compare Button */}
            {compareList.length > 0 && (
              <Button
                onClick={() => setShowComparison(!showComparison)}
                variant={showComparison ? "secondary" : "default"}
              >
                Compare ({compareList.length})
              </Button>
            )}
          </div>
        </CardContent>
      </Card>
      
      {/* Comparison View */}
      {showComparison && comparisonData && (
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Tool Comparison</CardTitle>
          </CardHeader>
          <CardContent>
            <ToolComparison
              tools={comparisonData.tools}
              onRemoveTool={removeFromComparison}
            />
          </CardContent>
        </Card>
      )}
      
      {/* Tools Grid */}
      {isLoading ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {[...Array(6)].map((_, i) => (
            <Card key={i} className="h-[400px] animate-pulse">
              <CardContent className="p-6">
                <div className="h-full bg-gray-200 rounded" />
              </CardContent>
            </Card>
          ))}
        </div>
      ) : error ? (
        <Card>
          <CardContent className="p-6 text-center">
            <p className="text-red-500">Error loading tools. Please try again.</p>
          </CardContent>
        </Card>
      ) : (
        <>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {data?.tools.map((tool: ITool) => (
              <ToolCard
                key={tool._id}
                tool={tool}
                onCompare={handleCompare}
                isComparing={compareList.includes(tool._id)}
              />
            ))}
          </div>
          
          {/* Pagination */}
          {data?.pagination && data.pagination.totalPages > 1 && (
            <div className="mt-8 flex justify-center">
              <nav className="flex space-x-2">
                {[...Array(data.pagination.totalPages)].map((_, i) => (
                  <Button
                    key={i}
                    variant={data.pagination.page === i + 1 ? "default" : "outline"}
                    size="sm"
                    onClick={() => {
                      // Handle pagination
                    }}
                  >
                    {i + 1}
                  </Button>
                ))}
              </nav>
            </div>
          )}
        </>
      )}
    </div>
  );
}
