// src/components/tools/ToolCard.tsx
'use client';

import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { ExternalLink, Star, TrendingUp } from 'lucide-react';
import { ITool } from '@/models/Tool';

interface ToolCardProps {
  tool: ITool;
  onCompare?: (toolId: string) => void;
  isComparing?: boolean;
}

export default function ToolCard({ tool, onCompare, isComparing }: ToolCardProps) {
  return (
    <Card className="h-full flex flex-col hover:shadow-lg transition-shadow">
      <CardHeader>
        <div className="flex items-start justify-between">
          <div className="flex items-center space-x-3">
            {tool.logo && (
              <Image
                src={tool.logo}
                alt={tool.name}
                width={48}
                height={48}
                className="rounded-lg"
              />
            )}
            <div>
              <CardTitle className="text-lg">{tool.name}</CardTitle>
              <div className="flex items-center space-x-2 mt-1">
                <Badge variant="outline" className="text-xs">
                  {tool.category.replace(/_/g, ' ')}
                </Badge>
                {tool.isFeatured && (
                  <Badge variant="default" className="text-xs">
                    Featured
                  </Badge>
                )}
              </div>
            </div>
          </div>
        </div>
        <CardDescription className="mt-2">{tool.description}</CardDescription>
      </CardHeader>
      
      <CardContent className="flex-1 flex flex-col">
        {/* Features preview */}
        <div className="flex-1">
          <div className="space-y-2 mb-4">
            <div className="flex flex-wrap gap-1">
              {tool.features.slice(0, 3).map((feature, index) => (
                <Badge key={index} variant="secondary" className="text-xs">
                  {feature}
                </Badge>
              ))}
              {tool.features.length > 3 && (
                <Badge variant="secondary" className="text-xs">
                  +{tool.features.length - 3} more
                </Badge>
              )}
            </div>
          </div>
          
          {/* Platforms */}
          <div className="flex items-center space-x-2 text-sm text-muted-foreground mb-4">
            <span>Available on:</span>
            {tool.platforms.slice(0, 3).map((platform, index) => (
              <span key={index}>{platform}</span>
            ))}
          </div>
          
          {/* Best for */}
          <div className="flex flex-wrap gap-1 mb-4">
            {tool.bestFor.map((target, index) => (
              <Badge key={index} variant="outline" className="text-xs">
                {target}
              </Badge>
            ))}
          </div>
        </div>
        
        {/* Pricing info */}
        <div className="border-t pt-4 mb-4">
          <div className="flex items-center justify-between">
            <span className="text-sm font-medium">
              {tool.pricing.isFree ? 'Free' : 
               tool.pricing.hasFreeTier ? 'Freemium' : 
               `₹${tool.pricing.startingPrice}/mo`}
            </span>
            <div className="flex items-center space-x-1 text-sm text-muted-foreground">
              <TrendingUp className="h-3 w-3" />
              <span>{tool.totalViews} views</span>
            </div>
          </div>
        </div>
        
        {/* Actions */}
        <div className="flex items-center space-x-2">
          <Button asChild className="flex-1">
            <Link href={`/tools/${tool.slug}`}>
              View Details
            </Link>
          </Button>
          <Button
            variant="outline"
            size="icon"
            asChild
          >
            <a href={tool.website} target="_blank" rel="noopener noreferrer">
              <ExternalLink className="h-4 w-4" />
            </a>
          </Button>
          {onCompare && (
            <Button
              variant={isComparing ? "secondary" : "outline"}
              size="sm"
              onClick={() => onCompare(tool._id)}
            >
              {isComparing ? "Added" : "Compare"}
            </Button>
          )}
        </div>
      </CardContent>
    </Card>
  );
}